class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: "",
    };
  }
  handleChange = (e) => {
    const value = e.target.value;
    this.setState({
      searchText: value,
    });
  };
  filterFriend = () => {
    return friendList.filter((item) => {
      return item.name
        .toLowerCase()
        .includes(this.state.searchText.toLowerCase());
    });
  };
  render() {
    const filtered = this.filterFriend();
    return (
      <div>
        <h1>Online Friend Search</h1>
        <input
          placeholder="search"
          onChange={this.handleChange}
          value={this.state.searchText}
        />
        {filtered.map((item, index) => {
          return (
            <FriendItem
              key={index}
              name={item.name}
              title={item.title}
              imageUrl={item.imageUrl}
            />
          );
        })}
      </div>
    );
  }
}
export default App;
